@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonial {
    background-color: $verde;
    font-size: 2.4rem;
    color: $blanco;
    border-radius: 0.5rem;

    blockquote {
        padding-left: 5rem;
        position: relative;
        
        
        &::before {
            content: '';
            background-image: url(../../../img/comilla.svg);
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            left: -2rem;
            // top: 3rem
        }
    }

    p {
        text-align: right;
        color: $blanco;
    }
}